.header {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-image: url("../img/thumb-1920-789385\ 1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header-contacts {
  margin-top: 60px;
}

.contacts {
  display: flex;
}

.contacts-link {
  padding: 34px;
}

.contacts-img {
  width: 24px;
  height: 24px;
}

.header-logo {
  max-width: 392px;
}

.header-logo {
  padding: 120px 0 140px;
}

.header-text {
  margin-bottom: 344px;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.header-title,
.header-subtitle {
  font-size: 40px;
}

.header-descr {
  font-size: 18px;
}

.header-bottom {
  background-color: #a21b1b;
  height: 106px;
}

@media @extraSmall-widths {
  .contacts-link {
    padding: 15px;
  }
  .header-logo {
    padding: 60px;
  }
  .header-text {
    margin-bottom: 80px;
  }
}
