.form-wrapper {
  position: relative;
  padding-top: 270px;
  background-image: url("../img/form-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.form-title,
.fogm-descr {
  color: #000;
  text-align: center;
}

.form-title {
  font-weight: 700;
  font-size: 30px;
  text-transform: uppercase;
}

.fogm-descr {
  font-size: 18px;
  line-height: 22px;
}

.form {
  margin: 0 auto;
  padding: 40px 0 100px;
  width: 100%;
  max-width: 375px;
}

.input-block {
  margin: 0 auto;
  padding: 20px 0;
  width: 90%;
  display: block;
}

.form-input-descr {
  display: block;
  color: #000;
}

.form-input,
.form-input-phone {
  padding: 10px;
  width: 100%;
  border: 1px solid #0b225c;
  box-sizing: border-box;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
  outline: #0b225c;
}

.checkbox-wrapper {
  display: flex;
  align-items: flex-start;
  margin: 0 5% 40px;
}

.checkbox {
  margin-right: 20px;
  color: #0b225c;
  outline: #0b225c;
}

.checkbox-descr {
  margin: 0;
  color: #000;
  font-size: 12px;
  line-height: 15px;
}

.btn-wrapper {
  text-align: center;
}

.form-btn {
  padding: 20px 0;
  width: 90%;
  color: #fff;
  background-color: #0b225c;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  cursor: pointer;
}

.intl-tel-input.allow-dropdown input,
.intl-tel-input.allow-dropdown input[type="tel"] {
  width: 339px;
}

.intl-tel-input .country-list .country-name {
  color: #000;
}

.errors-block {
  // margin-left: -50px !important;
  color: #f44d4d;
}

// .iti-flag {
//   background-image: url("/intlTel/img/flags.png");
// }

.success {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);
  font-size: 36px;
  font-weight: bold;
  line-height: 1;
  color: #ffffff;
  text-align: center;
  z-index: 1200;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s;
}
.success.opened {
  visibility: visible;
  opacity: 1;
}

@media @small-widths {
  .form-wrapper {
    padding-top: 410px;
  }
}
