.container {
  width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}
@media screen and (max-width: 1199px) {
  .container {
    width: 970px;
  }
}
@media screen and (max-width: 991px) {
  .container {
    width: 750px;
  }
}
@media screen and (max-width: 767px) {
  .container {
    width: 100%;
    padding: 0 10px;
  }
}
body {
  background-color: #fff;
  color: #fff;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Montserrat", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
* {
  box-sizing: border-box;
}
.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background-color: #e2e2e2;
  color: #6e747c;
  font-weight: 700;
}
.select2-container--default {
  text-align: center;
  color: #6e747c;
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: #6e747c;
}
.select2-results__options {
  height: 150px;
  overflow-y: auto;
}
.select2-container--default .select2-selection--single {
  width: 345px;
  margin: 0 auto;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  right: 5px;
}
.select2-results__options::-webkit-scrollbar {
  width: 8px;
}
.select2-results__options::-webkit-scrollbar-thumb {
  background-clip: padding-box;
  background-color: #ccc;
  border: 5px solid rgba(0, 0, 0, 0);
  border-bottom: 10px;
}
a {
  text-decoration: none;
}
.form-wrapper {
  position: relative;
  padding-top: 270px;
  background-image: url("../img/form-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.form-title,
.fogm-descr {
  color: #000;
  text-align: center;
}
.form-title {
  font-weight: 700;
  font-size: 30px;
  text-transform: uppercase;
}
.fogm-descr {
  font-size: 18px;
  line-height: 22px;
}
.form {
  margin: 0 auto;
  padding: 40px 0 100px;
  width: 100%;
  max-width: 375px;
}
.input-block {
  margin: 0 auto;
  padding: 20px 0;
  width: 90%;
  display: block;
}
.form-input-descr {
  display: block;
  color: #000;
}
.form-input,
.form-input-phone {
  padding: 10px;
  width: 100%;
  border: 1px solid #0b225c;
  box-sizing: border-box;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
  outline: #0b225c;
}
.checkbox-wrapper {
  display: flex;
  align-items: flex-start;
  margin: 0 5% 40px;
}
.checkbox {
  margin-right: 20px;
  color: #0b225c;
  outline: #0b225c;
}
.checkbox-descr {
  margin: 0;
  color: #000;
  font-size: 12px;
  line-height: 15px;
}
.btn-wrapper {
  text-align: center;
}
.form-btn {
  padding: 20px 0;
  width: 90%;
  color: #fff;
  background-color: #0b225c;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  cursor: pointer;
}
.intl-tel-input.allow-dropdown input,
.intl-tel-input.allow-dropdown input[type="tel"] {
  width: 339px;
}
.intl-tel-input .country-list .country-name {
  color: #000;
}
.errors-block {
  color: #f44d4d;
}
.success {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);
  font-size: 36px;
  font-weight: bold;
  line-height: 1;
  color: #ffffff;
  text-align: center;
  z-index: 1200;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s;
}
.success.opened {
  visibility: visible;
  opacity: 1;
}
@media screen and (max-width: 991px) {
  .form-wrapper {
    padding-top: 410px;
  }
}
.gray {
  color: rgba(0, 0, 0, 0.5);
}
h1,
h2 {
  font-size: 35px;
}
h3,
h4 {
  margin: 0;
  padding: 0;
}
.header {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-image: url("../img/thumb-1920-789385\ 1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header-contacts {
  margin-top: 60px;
}
.contacts {
  display: flex;
}
.contacts-link {
  padding: 34px;
}
.contacts-img {
  width: 24px;
  height: 24px;
}
.header-logo {
  max-width: 392px;
}
.header-logo {
  padding: 120px 0 140px;
}
.header-text {
  margin-bottom: 344px;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
.header-title,
.header-subtitle {
  font-size: 40px;
}
.header-descr {
  font-size: 18px;
}
.header-bottom {
  background-color: #a21b1b;
  height: 106px;
}
@media screen and (max-width: 767px) {
  .contacts-link {
    padding: 15px;
  }
  .header-logo {
    padding: 60px;
  }
  .header-text {
    margin-bottom: 80px;
  }
}
.footer {
  background-color: #0b225c;
}
.footer-contacts {
  justify-content: center;
  padding: 68px 0;
}
.benefits {
  margin-top: -190px;
  color: #000;
  background: linear-gradient(180deg, #ffffff 0%, #f9f9f9 100%);
  box-shadow: 0px 0px 60px rgba(11, 34, 92, 0.15);
}
.benefits-title {
  padding: 60px 0;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  line-height: 37px;
  text-transform: uppercase;
}
.benefits-blocks {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding-bottom: 60px;
}
.benefits-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 10px;
  text-align: center;
  width: 100%;
  max-width: 170px;
  border: 1px solid #a0a0a02e;
}
.benefits-block-img {
  margin: 30px 0 20px;
  max-width: 40px;
}
.benefits-block-title {
  margin-bottom: 25px;
  max-width: 120px;
  font-size: 12px;
  line-height: 15px;
  font-weight: 400;
}
.dispatch {
  margin: 0 auto;
  color: #000;
  width: 100%;
  max-width: 766px;
}
.dispatch-title {
  margin: 100px 0 52px;
  text-align: center;
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  text-transform: uppercase;
}
.dispatch-blocks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: inherit;
  margin-bottom: 100px;
}
.dispatch-block {
  padding: 41px;
  width: 33.3%;
  text-align: center;
  border: 1px solid #a0a0a02e;
}
.dispatch-block:nth-child(4),
.dispatch-block:nth-child(5) {
  width: 50%;
}
.dispatch-block:nth-child(1) .dispatch-img {
  max-width: 120px;
}
.dispatch-img {
  margin: 0 auto;
  max-width: 175px;
}
.questions {
  position: absolute;
  width: 100%;
  z-index: 1;
  background-image: url("../img/Rectangle.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.questions-blocks {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 60px 0 130px;
}
.questions-block {
  padding: 10px;
}
.questions-block-title {
  text-align: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
.questions-block-descr {
  font-weight: 300;
}
@media screen and (max-width: 991px) {
  .dispatch-blocks {
    flex-direction: column;
    align-items: center;
  }
  .dispatch-block {
    width: 100% !important;
  }
  .questions-blocks {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
