.benefits {
  margin-top: -190px;
  color: #000;
  background: linear-gradient(180deg, #ffffff 0%, #f9f9f9 100%);
  box-shadow: 0px 0px 60px rgba(11, 34, 92, 0.15);
}

.benefits-title {
  padding: 60px 0;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  line-height: 37px;
  text-transform: uppercase;
}

.benefits-blocks {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding-bottom: 60px;
}

.benefits-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 10px;
  text-align: center;
  width: 100%;
  max-width: 170px;
  border: 1px solid #a0a0a02e;
}

.benefits-block-img {
  margin: 30px 0 20px;
  max-width: 40px;
}

.benefits-block-title {
  margin-bottom: 25px;
  max-width: 120px;
  font-size: 12px;
  line-height: 15px;
  font-weight: 400;
}

.dispatch {
  margin: 0 auto;
  color: #000;
  width: 100%;
  max-width: 766px;
}

.dispatch-title {
  margin: 100px 0 52px;
  text-align: center;
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  text-transform: uppercase;
}

.dispatch-blocks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: inherit;
  margin-bottom: 100px;
}

.dispatch-block {
  padding: 41px;
  width: 33.3%;
  text-align: center;
  border: 1px solid #a0a0a02e;
}

.dispatch-block:nth-child(4),
.dispatch-block:nth-child(5) {
  width: 50%;
}

.dispatch-block:nth-child(1) .dispatch-img {
  max-width: 120px;
}

.dispatch-img {
  margin: 0 auto;
  max-width: 175px;
}

.questions {
  position: absolute;
  width: 100%;
  z-index: 1;
  background-image: url("../img/Rectangle.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.questions-blocks {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 60px 0 130px;
}

.questions-block {
  padding: 10px;
}

.questions-block-title {
  text-align: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.questions-block-descr {
  font-weight: 300;
}

// @media @medium-widths {
//   .benefits-blocks {
//     flex-direction: column;
//   }
// }

@media @small-widths {
  .dispatch-blocks {
    flex-direction: column;
    align-items: center;
  }
  .dispatch-block {
    width: 100% !important;
  }
  .questions-blocks {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
