body {
  background-color: #fff;
  color: #fff;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Montserrat", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

* {
  box-sizing: border-box;
}
